import { Link } from 'gatsby';
import React from 'react';

import content from '../../site/content/components/footer.json';

// const Icon = ({ brand: FaIcon }) => <FaIcon className="text-6xl" />;

interface Props {
  locale: string;
}

const Footer = ({ locale }: Props) => (
  <div
    className={`container py-8 bg-${content[locale].background_color} dark:bg-${content[locale].background_color_dark} text-${content[locale].text_color} dark:text-${content[locale].text_color_dark}`}
  >
    <div className="md:flex">
      <div id="logo" className="h-36">
        <img src={content[locale].logo} className="h-full mx-auto" />
      </div>
      <div id="categories" className="text-center md:flex md:flex-1 md:py-0 md:pl-8 md:justify-end">
        {content[locale].categories.map(category => (
          <div id="category" className="py-4 md:py-0 md:px-8">
            <h3
              className={`text-2xl text-${content[locale].category_color} dark:text-${content[locale].category_color_dark} p-2`}
            >
              {category.title}
            </h3>
            {category.items.map(item => (
              <div className="p-1">
                <a
                  href={item.link}
                  target={item.external ? '_blank' : ''}
                  rel={item.external ? 'noopener noreferrer' : ''}
                >
                  {item.label}
                </a>
              </div>
            ))}
          </div>
        ))}
      </div>
    </div>
    <hr className="text-blue border-2 border-blue mt-8 mb-4" />
    <div id="subpart" className="md:flex md:flex-row-reverse md:justify-between">
      <div id="socials" className="flex">
        {content[locale].socials.map(social => (
          <a href={social.link} className="h-14 w-14 p-2" target="_blank" rel="noopener noreferrer">
            <img
              src={social.icon}
              alt={social.id}
              className={`icon-${social.icon_color} dark:icon-${social.icon_color_dark}`}
            />
          </a>
        ))}
      </div>
      <p id="copyright" className="text-xs text-center pt-8">
        {content[locale].copyright}
      </p>
    </div>
  </div>
);

export default Footer;
